import React, {useState} from 'react';
import ZenCard from "../../common/card/ZenCard";
import ZenMenu, {ZenMenuItem} from "../../common/menu/ZenMenu";
import ZenDropdown from "../../common/dropdown/ZenDropdown";
import MyDesigns from "./MyDesigns";
import MyLists from "./MyLists";

function MyDesignLists() {
    const [activeTab, setActiveTab] = useState("myDesigns");
    const [status, setStatus] = useState("Live");

    const tabListNoTitle = [
        {
            key: 'myDesigns',
            tab: 'My Designs',
        },
        {
            key: 'myLists',
            tab: 'My Lists',
        },
    ];

    const menu = (
        <ZenMenu activeKey={status} onSelect={({key}) => setStatus(key)}>
            <ZenMenuItem key="Live">Live</ZenMenuItem>
            <ZenMenuItem key="Scheduled">Scheduled</ZenMenuItem>
            <ZenMenuItem key="Paused">Paused</ZenMenuItem>
        </ZenMenu>
    );
    const extra = (
        <React.Fragment>
            <span className={`text-14 text-unselected`}>Show: </span>
            <ZenDropdown overlay={menu}>
                <span className={`text 14 text-info underline`}>{status}</span>
            </ZenDropdown>
        </React.Fragment>
    );
    const myDesigns = (<MyDesigns />);
    const myLists = (<MyLists />);
    const contentListNoTitle = {
        myDesigns: myDesigns,
        myLists: myLists,
    };
    return (
        <ZenCard defaultActiveKey={activeTab} tabList={tabListNoTitle} tabBarExtraContent={extra}
                 onTabChange={(v) => setActiveTab(v)}>
            {contentListNoTitle[activeTab]}
        </ZenCard>
    );
}

export default MyDesignLists;