import classNames from 'classnames';

function Col({children, className, spacing}) {
    const classList = () => {
        return classNames([
            {
                [`px-15`]: spacing === 15,
                [`px-16`]: spacing === 16,
                [`px-17`]: spacing === 17,
            },
            className
        ])
    }
    return (
        <div className={classList()}>{children}</div>
    );
}

export default Col;
