import { Formik } from 'formik';
import ZenDialog from "../../common/dialog/ZenDialog";
import ZenForm from "../../common/form/ZenForm";
import ZenFormItem from "../../common/form/ZenFormItem";
import ZenFormLabel from "../../common/form/ZenFormLabel";
import ZenFormField from "../../common/form/ZenFormField";
import ZenButton from "../../common/button/ZenButton";
import {useState} from "react";

function NewMenuDesignerModal({onConfirm, ...props}) {
    const [value, setValue] = useState("");
    return (
        <ZenDialog title={`New Menu Designer`} maskClosable={false} {...props}>
            <Formik
                initialValues={{ title: '', item: '', tags: '' }}
                validate={values => {

                }}
                onSubmit={(values, { setSubmitting }) => {
                    setTimeout(() => {
                        alert(JSON.stringify(values, null, 2));
                        setSubmitting(false);
                    }, 400);
                }}
            >
                {({ isSubmitting }) => (
                    <ZenForm type={`vertical p-48`}>
                        <ZenFormItem>
                            <ZenFormLabel htmlFor={`title`} name={`Title`} />
                            <ZenFormField name={`title`} as={`text`} />
                        </ZenFormItem>

                        <ZenFormItem>
                            <ZenFormLabel htmlFor={`item`} name={`Select Item List`} />
                            <ZenFormField as={`select`} name={`item`} value={value} options={[
                                { value: 'foo', label: 'Foo' },
                                { value: 'bar', label: 'Bar' },
                            ]} onChange={(e) => {
                                setValue(e)
                            }}/>
                        </ZenFormItem>
                        <ZenFormItem>
                            <ZenFormLabel htmlFor={`tags`} name={`Tags`} />
                            <ZenFormField className={`h-78`} name={`tags`} as={`tags`} />
                        </ZenFormItem>
                        <div className={`text-right mt-40`}>
                            <ZenButton type={`primary-10`} size={`lg`} className={`rounded-37`} onClick={onConfirm}>Choose a template</ZenButton>
                        </div>
                    </ZenForm>
                )}
            </Formik>
        </ZenDialog>
    );
}

export default NewMenuDesignerModal;