import React from 'react';
import Select from 'rc-select';

function ZenInputTags({name}) {
    const ref = React.createRef();
    const onChange = () => {
        ref.current.focus();
    };
    return (
        <Select ref={ref} mode={`tags`} prefixCls={`zen-select`} open={false} name={name} onChange={onChange} />
    );
}
export default ZenInputTags;