import './dialog.scss';
import Dialog from 'rc-dialog';
import classNames from "classnames";
import CloseIcon from "../icon/CloseIcon";


function ZenDialog({title, onClose, children, size = '', ...props}) {
    const classList = () => {
        return classNames([{
            [`zen-dialog-${size}`]: size
        }])
    }
    return (
        <Dialog
            prefixCls={`zen-dialog`}
            className={classList()}
            title={title}
            onClose={onClose}
            closeIcon={<CloseIcon className={`text-icon hover:text-primary-navy`} />}
            {...props}>
            {children}
        </Dialog>
    );
}

export default ZenDialog;