import './badge.scss';
import classNames from 'classnames';

function ZenBadge({type, label, className}) {
    const classList = () => {
        return classNames([{
            'zen-badge-primary': type === 'primary',
            'zen-badge-warning': type === 'warning',
            'zen-badge-danger': type === 'danger',
        }], className)
    }
    return (
        <span className={`zen-badge ${classList()}`}>{label}</span>
    );
}

export default ZenBadge;
