import React, {useState} from 'react';
import ZenCard from "../../common/card/ZenCard";
import ZenDropdown from "../../common/dropdown/ZenDropdown";
import ZenMenu, {ZenMenuItem} from "../../common/menu/ZenMenu";

function Performance() {
    const [status, setStatus] = useState("Live");

    const menu = (
        <ZenMenu activeKey={status} onSelect={({key}) => setStatus(key)}>
            <ZenMenuItem key="Live">Live</ZenMenuItem>
            <ZenMenuItem key="Disable">Scheduled</ZenMenuItem>
            <ZenMenuItem key="Enable">Paused</ZenMenuItem>
        </ZenMenu>
    );
    const extra = (
        <React.Fragment>
            <span className={`text-14 text-unselected`}>Show: </span>
            <ZenDropdown overlay={menu}>
                <span className={`text 14 text-secondary underline`}>{status}</span>
            </ZenDropdown>
        </React.Fragment>
    );
    return (
        <ZenCard title={`Performance Overview`} extra={extra}>
            TBD
        </ZenCard>
    );
}

export default Performance;