import './card.scss';
import React from "react";
import classNames from "classnames";
import ZenTabs, {ZenTabPane} from "../tabs/ZenTabs";

function ZenCard({title, extra, tabList, tabBarExtraContent, onTabChange, tabProps, defaultActiveKey, children, className, ...props}) {
    let head = null;
    if (title || extra) {
        head = (
            <div className={`zen-card-head`}>
                <div className={`zen-card-head-wrapper`}>
                    {title && <div className={`zen-card-head-title`}>{title}</div>}
                    {extra && <div className={`zen-card-head-extra`}>{extra}</div>}
                </div>
            </div>
        );
    } else if (tabList) {
        const extraProps = {
            defaultActiveKey,
            ...tabProps,
        }
        const tabs =
            tabList && tabList.length ? (
                <ZenTabs
                    prefixCls={`zen-card-tabs`}
                    {...extraProps}
                    onChange={onTabChange}
                >
                    {tabList.map(item => (
                        <ZenTabPane tab={item.tab} disabled={item.disabled} key={item.key} />
                    ))}
                </ZenTabs>
            ) : null;
        head = (
            <div className={`zen-card-head`}>
                <div className={`zen-card-head-wrapper`}>
                    {tabs}
                    {tabBarExtraContent && <div className={`zen-card-head-extra`}>{tabBarExtraContent}</div>}
                </div>
            </div>
        );
    }
    const body = (
        <div className={`zen-card-body`}>
            {children}
        </div>
    );
    return (
        <div className={`zen-card ${classNames(className)}`}>
            {head}
            {body}
        </div>
    );
}

export default ZenCard;