import React, {Fragment} from "react";
import Row from "../common/flex-col/Row";
import Col from "../common/flex-col/Col";
import MyDesignLists from "./dashboard/MyDesignLists";
import Performance from "./dashboard/Performance";
import PlusIcon from "../common/icon/PlusIcon";

function Dashboard() {

    return (
        <Fragment>
            <Row spacing={17}>
                <Col className={`w-1/2`}>
                    <div className={`text-38 font-extrabold text-primary-navy`}>Welcome back, Matt!</div>
                    <div className={`text-unselected text-13 mt-14`}>Welcome to your dashboard. From here you can view your active menus and monitor their performance. Or you can get started on a brand new menu!</div>
                </Col>
                <Col className={`w-1/2`}>
                    <Row spacing={16}>
                        <Col className={`w-1/2`}>
                            <div className={`bg-white rounded-10 p-16 leading-42 flex text-16 font-bold shadow`}>
                                <div className={`bg-primary-10 rounded-circle text-primary mr-16`}><PlusIcon className={`w-14 h-14 m-14`} /></div>New Item List
                            </div>
                        </Col>
                        <Col className={`w-1/2`}>
                            <div className={`bg-white rounded-10 p-16 leading-42 flex text-16 font-bold shadow`}>
                                <div className={`bg-primary-10 rounded-circle text-primary mr-16`}><PlusIcon className={`w-14 h-14 m-14`} /></div>New Design
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
            <Row spacing={17} className={`mt-44`}>
                <Col className={`w-1/2`}>
                    <MyDesignLists />
                </Col>
                <Col className={`w-1/2`}>
                    <Performance />
                </Col>
            </Row>
        </Fragment>
    );
}

export default Dashboard;