import ZenDialog from "../../common/dialog/ZenDialog";
import ZenImage from "../../common/image/ZenImage";
import ZenButton from "../../common/button/ZenButton";

function TemplatePreviewModal({onClose, ...props}) {
    return (
        <ZenDialog title={`Tasty Italian Menu`} maskClosable={false} {...props} onClose={onClose}>
            <div className={`px-40`}>
                <div className={`mt-18`}>An elegant design that’s perfect for Italian restaurants and pizzerias.</div>
                <ZenImage src={`https://picsum.photos/520/299`} className={`w-full mt-20 rounded-10`} />
                <div className={`flex items-center`}>
                    <ul className={`flex-auto flex`}>
                        <li>item 1</li>
                        <li>item 1</li>
                        <li>item 1</li>
                        <li>item 1</li>
                        <li>item 1</li>
                    </ul>
                    <ZenButton type={`primary-10`} size={`lg`} className={`rounded-10`}>Use Template</ZenButton>
                </div>
            </div>
        </ZenDialog>
    )
}
export default TemplatePreviewModal;