import classNames from 'classnames';
import LogoIcon from "./LogoIcon";
import LogoTextIcon from "./LogoTextIcon";

function FullLogoIcon({className, ...props}) {
    return (
        <div className={`flex ${classNames(className)}`} {...props}>
            <LogoIcon className={`mr-8`} />
            <LogoTextIcon />
        </div>
    );
}

export default FullLogoIcon;

