import React from 'react';
import classNames from 'classnames';

function Row({children, className, spacing}) {
    const classList = () => {
        return classNames([
            {
                [`-mx-15`]: spacing === 15,
                [`-mx-16`]: spacing === 16,
                [`-mx-17`]: spacing === 17,
            },
            className
        ])
    }
    const childrenWithProps = React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
            return React.cloneElement(child, {"spacing": spacing});
        }
        return child;
    });
    return (
        <div className={`flex ${classList()}`}>{childrenWithProps}</div>
    );
}

export default Row;
