import React, {useState} from "react";
import PageTitle from "../components/PageTitle";
import ZenCard from "../common/card/ZenCard";
import ZenMenu, {ZenMenuItem} from "../common/menu/ZenMenu";
import ZenDropdown from "../common/dropdown/ZenDropdown";
import Row from "../common/flex-col/Row";
import Col from "../common/flex-col/Col";
import ZenImage from "../common/image/ZenImage";
import ZenBadge from "../common/badge/ZenBadge";
import MoreIcon from "../common/icon/MoreIcon";
import PlusIcon from "../common/icon/PlusIcon";
import NewMenuDesignerModal from "./modal/NewMenuDesignerModal";
import ChooseTemplateModal from "./modal/ChooseTemplateModal";
import TemplatePreviewModal from "./modal/TemplatePreviewModal";

function MenuDesigner() {
    const [status, setStatus] = useState("Last edited");
    const [isOpenMenuDesignerPopup, setIsOpenMenuDesignerPopup] = useState(false);
    const [isOpenChooseTemplatePopup, setIsOpenChooseTemplatePopup] = useState(false);

    const menu = (
        <ZenMenu activeKey={status} onSelect={({key}) => setStatus(key)}>
            <ZenMenuItem key="Last edited">Last edited</ZenMenuItem>
            <ZenMenuItem key="Scheduled">Scheduled</ZenMenuItem>
            <ZenMenuItem key="Paused">Paused</ZenMenuItem>
        </ZenMenu>
    );
    const extra = (
        <React.Fragment>
            <span className={`text-14 text-unselected`}>Sort by: </span>
            <ZenDropdown overlay={menu}>
                <span className={`text 14 text-info underline`}>{status}</span>
            </ZenDropdown>
        </React.Fragment>
    );
    return (
        <React.Fragment>
            <PageTitle title={`Menu Designer`} />
            <ZenCard title={`My Designs`} className={`mt-33`} extra={extra}>
                <div className={`px-34 pt-26 pb-40`}>
                    <Row spacing={17}>
                        <Col className={`w-1/3`}>
                            <div className={`border border-divider rounded-10 px-20 pt-24 pb-20`}>
                                <div className={`relative rounded overflow-hidden`}>
                                    <ZenImage src={`https://picsum.photos/seed/picsum/245/145`} className={`w-full`} />
                                    <ZenBadge className={`absolute top-0 right-0 rounded-bl`} type={`primary`} label={`LIVE`} />
                                </div>
                                <div className={`flex mt-18`}>
                                    <div className={`flex-auto text-14 leading-none`}>Paolo’s Italian</div>
                                    <div className={``}>
                                        <span>
                                            <MoreIcon className={`text-icon w-16 h-16 cursor-pointer`} />
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col className={`w-1/3`}>
                            <div className={`border border-divider rounded-10 px-20 pt-24 pb-20`}>
                                <div className={`relative rounded overflow-hidden`}>
                                    <ZenImage src={`https://picsum.photos/seed/picsum/245/145`} className={`w-full`} />
                                    <ZenBadge className={`absolute top-0 right-0 rounded-bl`} type={`primary`} label={`LIVE`} />
                                </div>
                                <div className={`flex mt-18`}>
                                    <div className={`flex-auto text-14 leading-none`}>Paolo’s Italian</div>
                                    <div className={``}>
                                        <span>
                                            <MoreIcon className={`text-icon w-16 h-16 cursor-pointer`} />
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col className={`w-1/3`}>
                            <div className={`border border-divider rounded-10 px-20 pt-24 pb-20`}>
                                <div className={`relative rounded overflow-hidden`}>
                                    <ZenImage src={`https://picsum.photos/seed/picsum/245/145`} className={`w-full`} />
                                    <ZenBadge className={`absolute top-0 right-0 rounded-bl`} type={`primary`} label={`LIVE`} />
                                </div>
                                <div className={`flex mt-18`}>
                                    <div className={`flex-auto text-14 leading-none`}>Paolo’s Italian</div>
                                    <div className={``}>
                                        <span>
                                            <MoreIcon className={`text-icon w-16 h-16 cursor-pointer`} />
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row spacing={17} className={`mt-28`}>
                        <Col className={`w-1/3`}>
                            <div className={`border border-divider rounded-10 px-20 pt-24 pb-20`}>
                                <div className={`relative rounded overflow-hidden`}>
                                    <ZenImage src={`https://picsum.photos/seed/picsum/245/145`} className={`w-full`} />
                                    <ZenBadge className={`absolute top-0 right-0 rounded-bl`} type={`primary`} label={`LIVE`} />
                                </div>
                                <div className={`flex mt-18`}>
                                    <div className={`flex-auto text-14 leading-none`}>Paolo’s Italian</div>
                                    <div className={``}>
                                        <span>
                                            <MoreIcon className={`text-icon w-16 h-16 cursor-pointer`} />
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col className={`w-1/3`}>
                            <div className={`h-full flex-auto border border-divider rounded flex flex-col justify-center items-center`}>
                                <div className={`bg-primary-10 rounded-circle text-primary w-66 h-66 cursor-pointer`} onClick={() => setIsOpenMenuDesignerPopup(true)}><PlusIcon className={`w-22 h-22 m-22`} /></div>
                                <span className={`text-14 mt-18`}>New Design</span>
                            </div>
                        </Col>
                    </Row>
                </div>
            </ZenCard>
            <NewMenuDesignerModal
                visible={isOpenMenuDesignerPopup}
                onConfirm={() => {
                    setIsOpenMenuDesignerPopup(false);
                    setIsOpenChooseTemplatePopup(true);
                }}
                onClose={() => setIsOpenMenuDesignerPopup(false)}/>
            <ChooseTemplateModal visible={isOpenChooseTemplatePopup} onClose={() => setIsOpenChooseTemplatePopup(false)} />
            <TemplatePreviewModal visible={false} />
        </React.Fragment>
    );
}

export default MenuDesigner;
