import {Field} from "formik";
import classNames from "classnames";
import ZenInputSelect from "./input/ZenInputSelect";
import ZenInputTags from "./input/ZenInputTags";

function ZenFormField({as, className, children, ...props}) {

    return (
        <div className={`zen-form-item-control`}>
            <div className={`zen-form-item-control-input`}>
                <div className={`zen-form-item-control-input-content`}>
                    {as === 'text' && <Field className={`zen-input ${classNames(className)}`} {...props}>{children}</Field>}
                    {as === 'select' && <Field as={ZenInputSelect} className={`${classNames(className)}`} {...props} />}
                    {as === 'tags' && <Field as={ZenInputTags} className={`${classNames(className)}`} {...props} />}
                </div>
            </div>
        </div>
    );
}

export default ZenFormField;