import './tabs.css';
import Tabs, { TabPane } from 'rc-tabs';

function ZenTabs({defaultActiveKey = 1, type, callback, prefixCls, ...props}) {
    return (
        <Tabs defaultActiveKey={defaultActiveKey}
              moreTransitionName="slide-up"
              prefixCls={prefixCls || `zen`}
              onChange={callback}
              {...props} />
    );
}

export {TabPane as ZenTabPane};
export default ZenTabs;