import { Scrollbars } from 'react-custom-scrollbars';
import FullLogoIcon from "../common/icon/FullLogoIcon";
import {Link} from "react-router-dom";
import MenuIcon from "../common/icon/MenuIcon";
import PencilIcon from "../common/icon/PencilIcon";
import HomeIcon from "../common/icon/HomeIcon";
import InsightIcon from "../common/icon/InsightIcon";
import HelpIcon from "../common/icon/HelpIcon";
import IntegrationIcon from "../common/icon/IntegrationIcon";
import PlusIcon from "../common/icon/PlusIcon";

function SideBar({className}) {
    return (
        <div className={`${className} flex-auto fixed top-0 bottom-0 z-1030 shadow bg-white flex flex-col`}>
            <FullLogoIcon className={`px-44 pb-55 pt-85`} />
            <Scrollbars>
                <div className={`px-44`}>
                    <ul>
                        <li>
                            <Link to={"/"}>
                                <span className={`text-primary-navy font-bold flex text-14`}><HomeIcon className={`mr-13 w-16 h-17`} />Dashboard</span>
                            </Link>
                        </li>
                        <li className={`pt-30`}>
                            <Link to={"/menu"}>
                                <span className={`text-unselected flex text-14 font-bold`}><MenuIcon className={`mr-13 w-16 h-16`} />Menu Manager</span>
                            </Link>
                            <ul className={`text-12 pl-24`}>
                                <li className={`mt-28`}>
                                    <Link to={"/menu"}>
                                        <span className={`text-primary-navy-light px-14 py-8 bg-grey-light rounded`}>My Lists</span>
                                    </Link>
                                </li>
                                <li className={`mt-28`}>
                                    <Link to={"/menu"}>
                                        <span className={`text-icon px-14 py-8`}>My Lists</span>
                                    </Link>
                                </li>
                                <li className={`mt-28`}>
                                    <Link to={"/menu"}>
                                        <span className={`text-icon px-14 py-8`}>My Ingredients</span>
                                    </Link>
                                </li>
                            </ul>
                        </li>
                        <li className={`pt-30`}>
                            <Link to={"/"}>
                                <span className={`text-unselected font-bold flex text-14`}><PencilIcon className={`mr-13 w-16 h-16`} />Menu Designer</span>
                            </Link>
                        </li>
                        <li className={`pt-30`}>
                            <Link to={"/"}>
                                <span className={`text-unselected font-bold flex text-14`}><IntegrationIcon className={`mr-13 w-16 h-18`} />Integrations</span>
                            </Link>
                        </li>
                        <li className={`pt-30`}>
                            <Link to={"/"}>
                                <span className={`text-unselected font-bold flex text-14`}><InsightIcon className={`mr-13 w-16 h-18`} />Reports</span>
                            </Link>
                        </li>
                        <li className={`pt-30`}>
                            <Link to={"/"}>
                                <span className={`text-unselected font-bold flex text-14`}><HelpIcon  className={`mr-10 w-19 h-19`}/>Helps</span>
                            </Link>
                        </li>
                    </ul>
                    <div className={`mt-124 w-164`}>
                        <div className={`bg-primary-5 rounded-10 p-16 leading-26 flex text-14 font-bold`}>
                            <div className={`bg-primary rounded-circle text-white mr-12`}><PlusIcon className={`w-10 h-10 m-8`} /></div>New Item List
                        </div>
                        <div className={`bg-primary-5 rounded-10 p-16 leading-26 flex text-14 font-bold mt-20`}>
                            <div className={`bg-primary rounded-circle text-white mr-12`}><PlusIcon className={`w-10 h-10 m-8`} /></div>New Design
                        </div>
                        <div className={`text-13 leading-40 flex font-bold mt-44 mb-50`}>
                            <div className={`text-center text-primary bg-primary-10 w-40 h-40 rounded-circle mr-8`}>MS</div>
                            <span className={`text-primary-navy`}>Matt Steedman</span>
                        </div>
                    </div>
                </div>
            </Scrollbars>
        </div>
    );
}

export default SideBar;
