import './tooltip.scss';
import Tooltip from 'rc-tooltip';

function ZenTooltip({children, ...props}) {

    const onVisibleChange = (visible) => {
        console.log('tooltip', visible); // eslint-disable-line no-console
    }
    return (
        <Tooltip
            prefixCls={`zen-tooltip`}
            onVisibleChange={onVisibleChange}
            placement={`right`}
            align={{
                offset: [0, '-20%'],
            }}
            {...props}>
            {children}
        </Tooltip>
    );
}

export default ZenTooltip;