import {useState} from "react";
import ZenItem from "../../common/list/ZenItem";
import ZenImage from "../../common/image/ZenImage";
import ZenBadge from "../../common/badge/ZenBadge";
import InfoIcon from "../../common/icon/InfoIcon";
import ZenTooltip from "../../common/tooltip/ZenTooltip";
import MoreIcon from "../../common/icon/MoreIcon";
import ZenList from "../../common/list/ZenList";
import ZenMenu, {ZenMenuItem} from "../../common/menu/ZenMenu";

function MyDesigns() {
    const [status, setStatus] = useState("Live");

    const listData = [];
    for (let i = 0; i < 5; i++) {
        listData.push({
            href: 'https://ant.design',
            title: `Paolo’s Italian Summer`,
            avatar: 'https://picsum.photos/seed/picsum/156/96',
            platforms: ['Digital Signage', 'Social', 'Web'],
            description:
                'Ant Design, a design language for background applications, is refined by Ant UED Team.',
            content:
                'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
        });
    }
    const menu = (
        <ZenMenu activeKey={status} onSelect={({key}) => setStatus(key)}>
            <ZenMenuItem key="Live">Live</ZenMenuItem>
            <ZenMenuItem key="Scheduled">Scheduled</ZenMenuItem>
            <ZenMenuItem key="Paused">Paused</ZenMenuItem>
        </ZenMenu>
    );
    return (
        <ZenList
            dataSource={listData}
            renderItem={item => (
                <ZenItem className={`flex py-26`}>
                    <div className={`w-156 mr-26`}><ZenImage className={`rounded`} src={item.avatar} /></div>
                    <div className={`flex-auto`}>
                        <div className={`text-primary-navy text-14`}>{item.title}</div>
                        <div className={`mt-14 text-10 text-grey-mid`}>Platforms: {item.platforms.join(', ')}</div>
                        <div className={`mt-14 flex`}>
                            <ZenBadge type={`warning`} className={`rounded mr-9`} label={`Scheduled`} />
                            <InfoIcon className={`w-21 h-21 mr-9 cursor-pointer`} />

                            <ZenTooltip
                                placement={`right`}
                                trigger={`click`}
                                overlay={menu}
                            >
                                    <span>
                                        <MoreIcon className={`w-21 h-21 cursor-pointer`} />
                                    </span>
                            </ZenTooltip>
                        </div>


                    </div>
                </ZenItem>
            )}
        />
    );
}

export default MyDesigns;