import './button.css'
import classNames from 'classnames';

function ZenButton({type, size, className, children, ...props}) {
    const classList = () => {
        return classNames([
            {
                btn: true,
                [`btn-${size}`]: size,
                [`btn-${type}`]: type,
            },
            className
        ]);
    }
    return (
        <button type={`button`} className={classList()} {...props}>{children}</button>
    );
}

export default ZenButton;