import './list.scss';
import React from 'react';
import classNames from 'classnames';

function ZenList({renderItem, rowKey, key, dataSource, isLoading, className, ...props}) {
    const renderInnerItem = (item, index) => {
        if (!renderItem) return null;

        return renderItem(item, index);
    };
    let childrenContent = isLoading && <div style={{ minHeight: 53 }} />;
    if (dataSource.length > 0) {
        const items = dataSource.map((item, index) => renderInnerItem(item, index));
        childrenContent = (
            <ul className={`${classNames(className)} zen-list`}>{items}
            </ul>
        );
    } else {
        childrenContent = (<div>Empty</div>);
    }
    return childrenContent
}

export default ZenList;